import { makeStyles } from '@mui/styles'
import React ,{ useRef, useState } from 'react'

import '../styles/product.css'

import MapsHomeWorkIcon from '@mui/icons-material/MapsHomeWork';

// Import Swiper React components
import { Swiper, SwiperSlide } from "swiper/react";
import 'swiper/swiper.min.css';
import 'swiper/swiper-bundle.css';
// import Swiper core and required modules
import SwiperCore, {
    Autoplay,Navigation,Pagination,Thumbs
  } from 'swiper';
import { Button } from '@mui/material';
import ProductPageProductList from '../components/Product/ProductLIstOne copy';
// install Swiper modules
SwiperCore.use([Autoplay,Navigation,Pagination,Thumbs]);


const useStyles = makeStyles((theme)=>({
    productPage:{
        marginTop: "100px",
    },
    mainContainer:{
        maxWidth: "1300px",
        margin:"0 auto",
        padding: "10px",
        width: "100%",
        display:"flex",
        flexDirection: "column"
    },
    productSection:{
        width: "100%",
        display:"flex",
        borderBottom: "1px solid lightgray",
    },
    rightPage:{
        width: "50%",
        height: "500px",
        padding: "5px",
    },
    mainProdoctImg:{
        height: "80%",
        width: "100%",
        '& .swiper-button-prev , .swiper-button-next':{
            background: "rgba(124, 124, 124, 0.418)",
            borderRadius: "20%",
            ' &:after':{
                fontSize: "15px",
                padding: "15px 10px",

            }
        }
    },
    minProductImg:{
        height: "20%",
        boxSizing: "border-box",
        padding: "10px 0",
    },

    leftPage:{
        width: "50%",
        height: "500px",
        padding: "10px",
        display: "flex",
        flexDirection: "column"
    },

    productTitle:{
        borderBottom: "1px solid lightgray",
        paddingBottom: "10px",
        '& h1':{
            fontSize: "18px",
            color: theme.palette.btnThree.main
        },
        '& h3':{
            fontSize: "14px"
        }
    },
    productInfo:{
        flex: "1",
        overflow: "auto",
        marginTop: "10px",
        background: "#fff",
        padding: "15px",
        borderRadius: "6px",
        '& span':{
            fontSize: "13px",
            fontWeight: "bold"
        },
        '& h3':{
            fontSize: "12px",
            fontWeight: "normal"
        }

    },
    productShoping:{
    },
    productOwner:{
        display: "flex",
        marginTop: "10px",
        background: "#fff",
        padding: "15px",
        borderRadius: "6px",
        alignItems: "flex-end",
        '& svg':{
            fontSize: "18px",
            color: theme.palette.btnThree.main,
            margin: "5px"
        },
        '& h4':{
            fontSize: "13px",
            margin: "0 5px"
        },
        '& h5':{
            fontSize: "13px",
            margin: "0 5px"
        }
    },
    productPriceContainer:{
        display: "flex",
        justifyContent: "space-between",
        marginTop: "10px",
        background: "#fff",
        padding: "15px",
        borderRadius: "6px",
    },
    price:{
        display: "flex",
        flex:"1",
        alignItems: "center",
        '& h5':{
            margin: "0 5px"
        }
    },
    addToBasketbtn:{
        flex: "1"

    },
    suggestion:{
        marginTop: "10px",
    },
    productSlide:{
        borderRadius: "8px",
        background: "#fff",
        textAlign: "center",
        display: "-webkit-box",
        display: "-ms-flexbox",
        display: "-webkit-flex",
        display: "flex",
        WebkitJustifyContent: "center",
        justifyContent: "center",
        WebkitBoxAlign: "center",
        msFlexAlign: "center",
        WebkitAlignItems: "center",
        alignItems: 'center',
        '& img':{
            display: "block",
            width: "100%",
            height: "100%",
            objectFit: "contain",
        }
    },


    // @media
    [theme.breakpoints.down("md")]:{
        productPage:{
            marginTop:"51px",
        },
    },


    [theme.breakpoints.down("sm")]:{
        productPage:{
            marginTop:"51px",
        },
        productSection:{
            width: "100%",
            display:"flex",
            flexDirection: "column",
            borderBottom: "1px solid lightgray",
        },
        rightPage:{
            width: "100%",
            height: "350px",
            padding: "5px",
        },
        leftPage:{
            width: "100%",
            height: "500px",
            padding: "10px",
            display: "flex",
            flexDirection: "column"
        },
        productOwner:{
            order: "1"
        },
        productPriceContainer:{

            order: "2"
        },
        productInfo:{
            order: "3"
        }
    }
}))

function Product() {

    const [thumbsSwiper, setThumbsSwiper] = useState(null);

    const classes = useStyles();
    return (
        <div className={classes.productPage}>
            <div className={classes.mainContainer}>
                <section className={classes.productSection}>
                    <div className={classes.rightPage}>
                        <Swiper className={classes.mainProdoctImg} style={{'--swiper-navigation-color': '#fff','--swiper-pagination-color': '#fff'}} loop={true} spaceBetween={10} navigation={true} thumbs={{ swiper: thumbsSwiper }} >
                            <SwiperSlide className={classes.productSlide} ><img src="/novinMobl/images/product-images/novinMobl-img (31).jpg" /></SwiperSlide>
                            <SwiperSlide className={classes.productSlide} ><img src="/novinMobl/images/product-images/novinMobl-img (32).jpg" /></SwiperSlide>
                            <SwiperSlide className={classes.productSlide} ><img src="/novinMobl/images/product-images/novinMobl-img (33).jpg" /></SwiperSlide>
                            <SwiperSlide className={classes.productSlide} ><img src="/novinMobl/images/product-images/novinMobl-img (34).jpg" /></SwiperSlide>
                        </Swiper>
                        <Swiper  className={`${classes.minProductImg} productSlider`} onSwiper={setThumbsSwiper} loop={true} spaceBetween={5} slidesPerView={4} freeMode={true} watchSlidesProgress={true}>
                            <SwiperSlide><img src="/novinMobl/images/product-images/novinMobl-img (31).jpg" /></SwiperSlide>
                            <SwiperSlide><img src="/novinMobl/images/product-images/novinMobl-img (32).jpg" /></SwiperSlide>
                            <SwiperSlide><img src="/novinMobl/images/product-images/novinMobl-img (33).jpg" /></SwiperSlide>
                            <SwiperSlide><img src="/novinMobl/images/product-images/novinMobl-img (34).jpg" /></SwiperSlide>
                        </Swiper>
                    </div>
                    <div className={classes.leftPage}>
                        <div className={classes.productTitle}>
                            <h1>  </h1>
                            <h3> : G-1</h3>
                        </div>
                        <div className={classes.productInfo}>
                            <span> :</span>
                            <h3>
                                   G-1     
                                                          .            mm 2              .             90        135 (  )    .
                            </h3>
                        </div>
                        <div className={classes.productShoping}>
                            <div className={classes.productOwner}>
                                <MapsHomeWorkIcon/>
                                <h4> :</h4>
                                <h5> </h5>
                            </div>
                            <div className={classes.productPriceContainer}>
                                <div className={classes.price}>
                                    <h4>1,700,000</h4>
                                    <h5></h5>
                                </div>
                                <Button className={classes.addToBasketbtn} variant="contained" color="secondary">
                                    <h4>   </h4>
                                </Button>
                            </div>
                        </div>
                    </div>
                </section>
                <div className={classes.suggestion}>
                    <ProductPageProductList/>
                </div>
            </div>
        </div>
    )
}

export default Product
